"use client";
import { trpc } from '../server/trpc/trpc';
import { useState } from 'react';

export default function Posts() {
    const utils = trpc.useUtils();
    const { data: posts, isLoading } = trpc.getPosts.useQuery();
    const addPost = trpc.addPost.useMutation({
        onSuccess: () => utils.getPosts.invalidate(),
    });
    const removePost = trpc.removePost.useMutation({
        onSuccess: () => utils.getPosts.invalidate(),
    });

    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [authorId, setAuthorId] = useState("");

    const handleAdd = (e: React.FormEvent) => {
        e.preventDefault();
        if (!title || !authorId) return;
        addPost.mutate({ title, content, authorId });
        setTitle("");
        setContent("");
        setAuthorId("");
    };

    if (isLoading) return <div>Loading...</div>;

    return (
        <div>
            <h2>Posts</h2>
            <form onSubmit={handleAdd} style={{ marginBottom: 16 }}>
                <input
                    placeholder="Title"
                    value={title}
                    onChange={e => setTitle(e.target.value)}
                    required
                />
                <input
                    placeholder="Content"
                    value={content}
                    onChange={e => setContent(e.target.value)}
                />
                <input
                    placeholder="Author ID"
                    value={authorId}
                    onChange={e => setAuthorId(e.target.value)}
                    required
                />
                <button type="submit" disabled={addPost.isPending}>Add Post</button>
            </form>
            <ul>
                {posts?.map(post => (
                    <li key={post.id}>
                        <strong>{post.title}</strong> by {post.authorId}
                        <button onClick={() => removePost.mutate({ id: post.id })} disabled={removePost.isPending} style={{ marginLeft: 8 }}>
                            Remove
                        </button>
                        <div>{post.content}</div>
                    </li>
                ))}
            </ul>
        </div>
    );
}
